import React from "react";
import UrboxHeader from "components/UrboxHeader";
import I18n from "translations";
import { StyleSheet, Text, View } from "react-native";
import TabComponent from "components/Tabs/TabComponent";
// import PartnerProgramsScreen from "./components/PartnerProgramsScreen";
// import UrStaffScreen from "./components/UrStaffScreen";
import UrPointFloatButton from "components/UrPointBubble";
import { get } from "lodash";
import Colors from "themes/Colors";

const PartnerLazy = React.lazy(() => import("./components/PartnerProgramsScreen"))
const PartnerSup = () => <React.Suspense fallback={<Text>Loading</Text>}><PartnerLazy /></React.Suspense>

const UrStaffLazy = React.lazy(() => import("./components/UrStaffScreen"))
const UrStaffSup = () => <React.Suspense fallback={<Text>Loading</Text>}><UrStaffLazy /></React.Suspense>

const PartnerProgramsTab = ({ navigation, route }) => {
  const onGoBack = () => navigation.goBack();
  const tabIndex = get(route, "params.tabIndex", 0);
  return (
    <View style={styles.container}>
      <UrboxHeader title={I18n.t("PartnerPrograms")} backOnPress={onGoBack} />
      <UrPointFloatButton />
      <TabComponent
        listLabels={[I18n.t("RedeemGifts"), "UrStaff"]}
        listComponents={[PartnerSup, UrStaffSup]}
        style={styles.flex}
        activeTabIndex={tabIndex}
        tabBarStyles={styles.tabBarStyles}
        customIndicatorWidth={"30%"}
        lazy
      />
    </View>
  );
};

const styles = StyleSheet.create({
  container: { flex: 1, backgroundColor: "white" },
  flex: { flex: 1 },
  tabBarStyles: {
    borderBottomWidth: 1,
    borderColor: Colors.darkGreyFour
  }
});

export default PartnerProgramsTab;
